.content-container {
  //   background-color: red;
  height: 100%;
  display: flex;
  flex-direction: row;
  gap: 16px;
  // padding: 0 16px;

  .exercise-video-wrapper {
    flex: 1 1 0;
    height: 100%;
    // padding: 16px;
    // height: calc(100vh - 166px);
    // overflow: hidden;
    border-radius: 16px;
    // margin-bottom: -4px;
    // width: 45%;
    // margin: 0 auto;
    // margin-bottom: 24px;
  }

  .video-skeleton-wrapper {
    flex: 1 1 0;

    .video-skeleton {
      position: relative;
      aspect-ratio: 1 / 1;
      border-radius: 16px;
      overflow: hidden;

      &::after {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        transform: translateX(-100%);
        animation: shimmer 1s infinite;
        content: '';
      }

      @keyframes shimmer {
        100% {
          transform: translateX(100%);
        }
      }
    }
  }

  .exercise-info-wrapper {
    flex: 2 1 0;
    height: calc(100vh - 166px);
    overflow: hidden;
    min-width: 0;

    // .exercise-content-wrapper {
    // height: 100%;

    .info-flex-container {
      display: flex;
      flex-direction: column;
      gap: 16px;

      .dialog-content-wrapper,
      .programs-wrapper {
        border-radius: 16px;
        padding: 16px;
      }
    }

    .exercise-video-wrapper {
      // padding: 0 126px;
      // height: 300px;
    }
    // }
  }
}
